import React, {useState, useEffect} from 'react';
import { Button, Modal, } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Card} from 'react-bootstrap';
import './projectModal.css'

function ProjectModal({msg, imageUrl}) {
    const [show, setShow] = useState(false);
    const [javaScriptProjects, setJavaScriptProjects] = useState('');
    const [reactProjects, setReactProjects] = useState('');
    const [htmlProjects, setHtmlProjects] = useState('');
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    useEffect(() => {
        fetch('https://api.github.com/users/vasivanov/repos', {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
            }
        }).then(res => {
            
            if(res.status !== 200 && res.status !== 201) {
                throw new Error('Failed');
            }
            return res.json();
        })
        .then(resData => {
            let jsArray = [];
            let reactArray = [];
            let htmlArray = [];
            resData.forEach(e => {
                if(e.name.includes('Back') || e.name.includes('JavaScript')){
                    jsArray.push({name: e.name, url: e.html_url});
                } else if(e.name.includes('Graph') || e.name.includes('React')) {
                    reactArray.push({name: e.name, url: e.html_url});
                } else if(e.name.includes('HTML') || e.name.includes('site')) {
                    htmlArray.push({name: e.name, url: e.html_url});
                }
            })
            setJavaScriptProjects(jsArray);
            setReactProjects(reactArray);
            setHtmlProjects(htmlArray);
        })
        .catch(err => {
            console.log(err);
        });
    }, [])
  
    return (
      <>
        {/* <button style={{backgroundColor: '#191D2B', border: 'none', color: 'white', borderRadius: '5px', padding: '0.3em 0.5em'}} variant="primary" onClick={handleShow}>
          {msg}
        </button> */}
         <Card onClick={handleShow} style={{ width: '18rem', backgroundColor: '#10121B', margin: '3em 0', cursor: 'pointer'}}>
            <Card.Img variant="top" src={imageUrl} />
            <Card.Body>
                <Card.Title>{msg}</Card.Title>
            </Card.Body>
        </Card>
        {msg === 'JavaScript Projects' && <Modal show={show} onHide={handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>{msg}</Modal.Title>
          </Modal.Header>
          <Modal.Body>
              {javaScriptProjects ?  javaScriptProjects.map(e => {
                  return(
                      <div className="repo-info" > 
                          <p>{e.name}</p>
                          <a rel="noopener noreferrer" target='_blank' href={e.url}>Link to repo</a>
                      </div>
                  )
              }) : null}
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>}
        {msg === 'React Projects' && <Modal show={show} onHide={handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>{msg}</Modal.Title>
          </Modal.Header>
          <Modal.Body>
              {reactProjects ?  reactProjects.map(e => {
                  return(
                      <div className="repo-info">
                          <p>{e.name}</p>
                          <a rel="noopener noreferrer" target='_blank' href={e.url}>Link to repo</a>
                      </div>
                  )
              }) : null}
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>}
        {msg === 'HTML 5 CSS 3 Projects' && <Modal show={show} onHide={handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>{msg}</Modal.Title>
          </Modal.Header>
          <Modal.Body>
              {htmlProjects ?  htmlProjects.map(e => {
                  return(
                      <div className="repo-info">
                          <p>{e.name}</p>
                          <a rel="noopener noreferrer" target='_blank' href={e.url}>Link to repo</a>
                      </div>
                  )
              }) : null}
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>}
        
      </>
    );
  }

export default ProjectModal;